@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@300;400;500;800;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: "DankMono";
  src: url(/static/media/DankMono-Regular.bca6428c.woff2) format("woff2"),
    url(/static/media/DankMono-Regular.de1b758f.woff) format("woff"),
    url(/static/media/DankMono-Regular.6d0dca36.ttf) format("truetype"),
    url(/static/media/DankMono-Regular.b742fa7b.svg#DankMono-Regular) format("svg");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "DankMono";
  src: url(/static/media/DankMono-Italic.3c4d3827.woff2) format("woff2"),
    url(/static/media/DankMono-Italic.a5b2b4f5.woff) format("woff"),
    url(/static/media/DankMono-Italic.42d2633a.ttf) format("truetype"),
    url(/static/media/DankMono-Regular.b742fa7b.svg#DankMono-Italic) format("svg");
  font-weight: 400;
  font-style: italic;
  font-display: swap;
}

* {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  --welcome-mobile-ht: 72px;
}

.App {
