/* BootStrap Override CSS START*/
html .carousel {
  --cardW: 30vw;
  --cardH: calc(var(--cardW) * 2 / 3);
  width: var(--cardW);
  height: var(--cardH);
  margin-top: 3.2vh;
  margin-left: 30vw;
  border-radius: 5px;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.4);
  overflow: hidden;
  font-family: "Raleway", sans-serif;
}

.carousel-indicators {
  display: none;
  position: absolute;
  margin-bottom: -30px;
}

html .carousel-control-next-icon,
html .carousel-control-prev-icon {
  margin-top: calc(var(--cardH) / -2);
}

html .card {
  width: var(--cardW);
  height: var(--cardH);
  border: none;
  border-radius: 5px;
}

html .card-title {
  font-weight: 800;
  display: none;
  font-family: "Raleway", sans-serif;
}

html .card-body {
  padding: 0.8rem;
  /* min-height: 8rem; */
  font-weight: 800;
  color: #576574;
  font-weight: 500;
  font-size: 1rem;
  letter-spacing: 0.02rem;
}

html .card-text {
  height: 8vh;
}

html .btn-primary {
  font-size: 1rem;
  margin-top: auto;
  font-family: "Raleway", sans-serif;
}

.card-img-top {
  height: calc(var(--cardH) / 2);
  object-fit: cover;
  /* None drag */
  user-select: none;
  -moz-user-select: none;
  -webkit-user-drag: none;
  -webkit-user-select: none;
  -ms-user-select: none;
}

@media (min-aspect-ratio: 560/500) {
  /* LANDSCAPE MODE*/
  @media (max-width: 900px) {
    /* LANDSCAPE MODE MOBILE*/
    html .btn-primary {
      display: none;
    }
    html .carousel {
      margin-left: 38vw;
      --cardW: 42vw;
      --cardH: 44vh;
      width: var(--cardW);
      height: var(--cardH);
    }
  }
  @media (min-width: 900px) {
    /* LANDSCAPE DESKTOP TYPICAL  */
    .card-image-href {
      pointer-events: none;
      cursor: default;
    }
  }
  @media (min-height: 750px) {
    /* LARGE SCREEN DESKTOP*/
    html .carousel {
      margin-top: 7.2vh;
    }
    html .card-text {
      height: 11vh;
    }
  }
}
@media (max-aspect-ratio: 560/500) {
  /* PORTRAIT MODE */
  .card-image-href {
    pointer-events: none;
    cursor: default;
  }
  @media (min-height: 900px) {
    /* HIGHRES PORTRAIT IPAD */
    html .card-text {
      height: 7vh;
    }
    html .carousel {
      margin-top: 4vh;
      margin-left: 0;
      --cardW: 60vw;
      --cardH: calc(var(--cardW) * 2 / 3);
      width: var(--cardW);
      height: var(--cardH);
      box-shadow: 2px 2px 3px rgba(0, 0, 0, 0.2);
    }
  }
  @media (max-height: 900px) {
    /* MOBILE PORTRAIT TYPICAL */
    html .card-text {
      height: 8vh;
    }
    html .carousel {
      margin-top: 2vh;
      margin-left: 0;
      --cardW: 82vw;
      --cardH: 38vh;
      width: var(--cardW);
      height: var(--cardH);
      box-shadow: 2px 2px 3px rgba(0, 0, 0, 0.2);
    }
  }
}
/* BootStrap Override CSS END*/

